import { Camera } from '@ionic-native/camera';
import { HttpProvider } from './../providers/data/data';
import { MessageHelper } from './../providers/message-helper';
import { Codes } from './../Utils/Codes';
import { DataValidation } from './../Utils/DataValidation';
import { BrowserModule } from '@angular/platform-browser';
import { ErrorHandler, NgModule } from '@angular/core';
import { IonicApp, IonicErrorHandler, IonicModule, NavController } from 'ionic-angular';

import { MyApp } from './app.component';

import { StatusBar } from '@ionic-native/status-bar';
import { SplashScreen } from '@ionic-native/splash-screen';
import { HttpClientModule } from '@angular/common/http';
import { Http, ConnectionBackend, HttpModule } from '@angular/http';
import { StringUtils } from '../Utils/StringUtils';




@NgModule({
  declarations: [
    MyApp,
    
  ],
  imports: [
    BrowserModule,
    IonicModule.forRoot(MyApp, {}, 
{
  links: [
    { loadChildren: '../pages/create-lead/create-lead.module.ngfactory#CreateLeadPageModuleNgFactory', name: 'CreateLeadPage', segment: 'create-lead', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/dashboard-tab/dashboard-tab.module.ngfactory#DashboardTabPageModuleNgFactory', name: 'DashboardTabPage', segment: 'dashboard-tab', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/filter-lead-management/filter-lead-management.module.ngfactory#FilterLeadManagementPageModuleNgFactory', name: 'FilterLeadManagementPage', segment: 'filter-lead-management', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/edit-service-request/edit-service-request.module.ngfactory#EditServiceRequestPageModuleNgFactory', name: 'EditServiceRequestPage', segment: 'edit-service-request', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/filter-service-management/filter-service-management.module.ngfactory#FilterServiceManagementPageModuleNgFactory', name: 'FilterServiceManagementPage', segment: 'filter-service-management', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/home/home.module.ngfactory#HomePageModuleNgFactory', name: 'HomePage', segment: 'home', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/lead-detail/lead-detail.module.ngfactory#LeadDetailPageModuleNgFactory', name: 'LeadDetailPage', segment: 'lead-detail', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/leads/leads.module.ngfactory#LeadsPageModuleNgFactory', name: 'LeadsPage', segment: 'leads', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/login/login.module.ngfactory#LoginPageModuleNgFactory', name: 'LoginPage', segment: 'login', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/manage-lead/manage-lead.module.ngfactory#ManageLeadPageModuleNgFactory', name: 'ManageLeadPage', segment: 'manage-lead', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/service-management/service-management.module.ngfactory#ServiceManagementPageModuleNgFactory', name: 'ServiceManagementPage', segment: 'service-management', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/ticket/ticket.module.ngfactory#TicketPageModuleNgFactory', name: 'TicketPage', segment: 'ticket', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/service-ticket-detail/service-ticket-detail.module.ngfactory#ServiceTicketDetailPageModuleNgFactory', name: 'ServiceTicketDetailPage', segment: 'service-ticket-detail', priority: 'low', defaultHistory: [] }
  ]
}),
    HttpClientModule,
    HttpModule
  ],
  bootstrap: [IonicApp],
  entryComponents: [
    MyApp,
  ],
  providers: [
    StatusBar,
    SplashScreen,
    DataValidation,
    Codes,
    MessageHelper,
    HttpProvider,
    StringUtils,
    Camera,
    {provide: ErrorHandler, useClass: IonicErrorHandler}
  ]
})
export class AppModule {}



